	module.exports = {"data":{"page":{"id":"/Users/sweet/blog/content/pages/1--about/index.md absPath of file >>> MarkdownRemark","html":"<p>This is a demo site of the <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">Personal<strong>Blog</strong></a> <a href=\"https://www.gatsbyjs.org/\">GatsbyJs</a> starter.</p>\n<p>The starter is a ready to use, easy to customize ‘like theme’ starter. It has everything what blog should have and some more.</p>\n<h3>Standard features:</h3>\n<ul>\n<li>Easy editable content in <strong>Markdown</strong> files (posts, pages and parts)</li>\n<li><strong>SEO</strong> (sitemap generation, robot.txt, meta and OpenGraph Tags)</li>\n<li><strong>Social</strong> sharing (Twitter, Facebook, Google, LinkedIn)</li>\n<li><strong>Comments</strong> (Facebook)</li>\n<li><strong>Images</strong> lazy loading and <code>webp</code> support (gatsby-image)</li>\n<li>Post <strong>categories</strong> (post list filtering)</li>\n<li>Full text <strong>searching</strong> (Algolia)</li>\n<li><strong>Contact</strong> form (Netlify form handling)</li>\n<li><strong>RSS</strong> feed</li>\n<li><strong>PWA</strong> (manifes.json, offline support, favicons)</li>\n<li>Google <strong>Analytics</strong></li>\n<li><strong>Favicons</strong> generator (node script)</li>\n<li><strong>Material UI</strong> (@next)</li>\n</ul>\n<h3>Advanced features:</h3>\n<ul>\n<li>Easy customizable base <strong>styles</strong> via <code>theme</code> object (fonts, colors, sizes)</li>\n<li><strong>Components</strong> leazy loading (social sharing, comments)</li>\n<li><strong>ESLint</strong> (google config)</li>\n<li><strong>Prettier</strong> code styling</li>\n<li>Custom webpack <code>CommonsChunkPlugin</code> settings</li>\n<li>Webpack <code>BundleAnalyzerPlugin</code></li>\n<li>Contact <strong>form validation</strong> (react-material-ui-form-validator)</li>\n</ul>\n<h2>About me</h2>\n<p>My name is <strong>Greg</strong>. I am a front-end web developer. I love working with <a href=\"https://www.gatsbyjs.org/\">GatsbyJs</a>. I love the UX it delivers out of the box for the end users and DX it provides to me as a developer. I decided to share the love by contributing in this way, sharing my code as starters. I hope somebody find that useful.</p>\n<p>For sure it’s not my last <a href=\"/starters\">Gatsby starter</a>. If you want to be in touch follow my on Twitter <a href=\"https://twitter.com/greglobinski\">@greglobinski</a></p>\n<p>PS: Please be understanding. The English language is not my native language. If you find any bug or not clear words you can always do PR to <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">gatsby-starter-personal-blog</a>, the repo contains the actual code of this demo.</p>","frontmatter":{"title":"About"}},"footnote":{"id":"/Users/sweet/blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>this is a demo site of the <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">gatsby-starter-personal-blog</a></li>\n<li>built by <a href=\"https://www.greglobinski.com\">greg lobinski</a></li>\n<li>with <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> &#x26; <a href=\"https://reactjs.org\">React</a></li>\n<li>source code on <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">GitHub</a></li>\n<li>deliverd by <a href=\"https://www.netlify.com/\">Netlify</a></li>\n<li>photos by <a href=\"https://unsplash.com\">unsplash.com</a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/about/"}}

